import React,{Component} from "react";
import axios from "axios";
import { URL } from "../../config";
import Row from "./row";
import ReactLoading from 'react-loading'
class Table extends Component {

  constructor(props){
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items:[]
    }
  }
  // componentWillMount(){
  //   axios.get(`${URL}/api/index/company`).then((res)=>{ 
  //     this.setState({
  //       FData : res.data
  //     })  
  //   })
  // }
  Loading = ({ type, color }) => (
    <ReactLoading type={type} color={color} height={300} width={200} />
);
  componentDidMount() {
    axios.get(`${URL}/api/index/company`)
      .then(res => res.data)
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            items: result
          });
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
  }

  //this function will delete a contact
deleteCompany = (id)=>{
  //using 'id' delete comp
  axios.delete(`${URL}/api/index/company/${id}`).then((res)=>{
    const filterCompany = this.state.items.filter((item)=>{
      return item.Cid !== id
    })
    
  this.setState({
    items: filterCompany
  })
  this.renderTableBody(this.state.items)
  })
}
  
   renderTableBody(datas){
    const template = datas.map((item,i)=>(
       <Row className="item" key={i} data={item} deleteCompany = {this.deleteCompany}/>
       ))
    return template
  }
  render(){
    const { error, isLoaded, items } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div className="offset-4">{this.Loading({type:'spin',color:'#ffdefd'})}</div>;
    } else {
      return (
        <table className="table table-hover offset-1 mt-2">
      <thead className="thead-dark">
        <tr>
          <th>Name</th>
          <th>Category</th>
          <th>Email</th>
          <th>PhoneNumber</th>
          <th>more info</th>
          <th>delete</th>
        </tr>
      </thead>
          {this.renderTableBody(items)}
          </table>
      );
    }
  }
};


export default Table;