import React from "./sidebar";

const SidebarTemplate = () => {
  return (
    <div className="card" style={{ width: "18rem" }}>
      <div className="card-header bg-dark text-light">Company Categories</div>
      <ul className="list-group list-group-flush  mt-3">
        <li className="list-group-item">
          <button className="btn btn-primary btn-block">Governmental</button>
        </li>
        <li className="list-group-item ">
          <button className="btn btn-primary btn-block">NGO's</button>
        </li>
        <li className="list-group-item  ">
          <button className="btn btn-primary btn-block">Commercials</button>
        </li>
        <li className="list-group-item ">
          <button className="btn btn-primary btn-block">Residentials</button>
        </li>
        <li className="list-group-item ">
          <button className="btn btn-primary btn-block">Others</button>
        </li>
      </ul>
    </div>
  );
};

export default SidebarTemplate;
