import React from 'react'
import {Link} from 'react-router-dom'

const Row = (props)=>{
    const item = props.data
   return (
        <tr key={item.Cid}>
          <td>{item.Cname}</td>
          <td>{item.Ccategory}</td>
          <td>{item.Cemail}</td>
          <td>{item.CphoneNumber}</td>
          <td>
            <Link to={{
                pathname:`/company/${item.Cid}`
            }}>more info</Link>
          </td>
          <td><button onClick={()=>{props.deleteCompany(item.Cid)}} className="btn btn-danger"><i className="fa fa-trash" aria-hidden="true"></i></button></td>
        </tr>
   )
}

export default Row